import React, { Component } from 'react';

export default class Add extends Component {
    state={
        Id:"",
        Title:"",
        Status:"Pending"
    };
    addId=(event)=>{
        console.log(event.target.value);
        this.setState({
            
            Id:event.target.value
        });
    };
    addTitle=(event)=>{
        this.setState({
            
            Title:event.target.value
        }); 
    };
    statusChange=(event)=>{
        this.setState({
            
            Status:event.target.value
        });
    };
    handleTodochange=(event)=>{
        event.preventDefault(); //to preventDefaultvalue and stay longer on console       
        this.props.onAdd({
            Id:this.state.Id,
            Title:this.state.Title,
            Status:this.state.Status
        });
        this.setState({
             Id:"",
            Title:"",
            Status:"Pending"
        });
    
    };
    render() {
        return (
            <form onSubmit={this.handleTodochange} >
                <div  onChange={this.addId} className="form-group"  >
                    <input value={this.state.Id} className="form-control" placeholder="Enter the Id">
                    </input>
                </div>

                <div  onChange={this.addTitle} className="form-group"  >
                    <input value={this.state.Title} className="form-control" placeholder="Enter the Title">
                    </input>
                </div>
                <div onChange={this.statusChange} className="form-group" >
                    <select className="form-control">
                    <option ></option>
                        <option value="Done">Done</option>
                        <option value="Pending">Pending</option>
                    </select>
                </div>


                <button type="submit" className="form-control btn btn-primary" >Todo Add</button>


            </form>

        );
    }
}