import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import TodoAdd from './TodoAdd';

class TodoList extends Component {
    state = {
        todo: [
            { Id: '1', Title: 'My Work', Status: 'Done' },
            { Id: '2', Title: 'My College', Status: 'Pending' }

        ]
    };
    deleteItem = (arg) => {
        const filterItem = this.state.todo.filter(x => x.Id !== arg.Id)
        this.setState({
            todo: filterItem
        });
    };
    addTodo=(c)=>{
        this.setState({
            todo:[...this.state.todo,c]        
        });       
    };
    editTodo = (x) => {
        this.setState(() => ({
            todo: this.state.todo.map(eve => {
                if (eve.Id === x.Id) {
                    return {
                        ...eve,
                        Status: eve.Status === "Done" ? "Pending" : "Done"
                    };
                }
                else {
                    return eve;
                }
            })
        }));
    };
    render() {
        return (
            <div>
                <TodoAdd onAdd={this.addTodo} ></TodoAdd>
                <h1>Your Todo List</h1>
                <table className="table">
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>Title</th>
                            <th>Status</th>
                            <th>Action</th>

                        </tr>
                    </thead>
                    <tbody>
                        {this.state.todo.map((x) => {
                            return (
                                <tr>
                                    <td>{x.Id}</td>
                                    <td>{x.Title}</td>
                                    <td style={{ color: x.Status === "Done" ? "green" : "red" }} >{x.Status}</td>
                                    <td>
                                        <button className="btn btn-primary" onClick={() => this.deleteItem(x)}>
                                            <FontAwesomeIcon icon="trash" ></FontAwesomeIcon>
                                        </button>
                                        |
                                        <button className="btn btn-primary" onClick={() => this.editTodo(x)}>

                                            <FontAwesomeIcon icon="edit"></FontAwesomeIcon>
                                        </button>
                                    </td>


                                </tr>
                            );
                        })}
                    </tbody>
                </table>
            </div>

        );
    }
}

export default TodoList;